import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { provider } from 'web3-core'
import { getContract } from 'utils/erc20'
import { Button, Flex, Text } from 'uikit'
import { FarmWithStakedValue } from 'state/types'
import contracts from 'config/constants/contracts'
import { useFarmUser } from 'state/hooks'
import useI18n from 'hooks/useI18n'
import UnlockButton from 'components/UnlockButton'
import useApprove from 'hooks/useApprove'
import Countdown, { CountdownRenderProps } from 'react-countdown'
import StakeAction from './StakeAction'
import HarvestAction from './HarvestAction'

const Action = styled.div`
  padding-top: 16px;
`

const Divider = styled.div`
  background-color: ${({ theme }) => theme.colors.borderColor};
  height: 1px;
  margin: 28px auto;
  width: 100%;
`

interface FarmCardActionsProps {
  farm: FarmWithStakedValue
  ethereum?: provider
  account?: string
  earnToken?: string
}

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

const CardActions: React.FC<FarmCardActionsProps> = ({ farm, ethereum, account, earnToken }) => {
  const TranslateString = useI18n()
  const [requestedApproval, setRequestedApproval] = useState(false)
  const { allowance, tokenBalance, stakedBalance, earnings } = useFarmUser(farm.pid)
  const lpAddress = farm.lpAddresses[CHAIN_ID]
  const tokenAddress = contracts[farm.tokenSymbol][CHAIN_ID]
  const lpName = farm.lpSymbol.toUpperCase()
  const isApproved = account && allowance && allowance.isGreaterThan(0)

  const lpContract = useMemo(() => getContract(ethereum as provider, farm.isTokenOnly ? tokenAddress : lpAddress), [
    ethereum,
    lpAddress,
    tokenAddress,
    farm.isTokenOnly,
  ])

  const { onApprove } = useApprove(farm.type, lpContract)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const renderApprovalOrStakeButton = () => {
    return isApproved ? (
      <StakeAction
        stakedBalance={stakedBalance}
        tokenBalance={tokenBalance}
        farm={farm}
      />
    ) : (
      <Button mt="8px" fullWidth disabled={requestedApproval} onClick={handleApprove}>
        {TranslateString(999, 'Approve Contract')}
      </Button>
    )
  }

  const renderer = (countdownProps: CountdownRenderProps) => {
    const { days, hours, minutes, seconds } = countdownProps
    const paddedSeconds = seconds < 10 ? `0${seconds}` : seconds
    const paddedMinutes = minutes < 10 ? `0${minutes}` : minutes
    const paddedHours = hours < 10 ? `0${hours}` : hours
    const paddedDays = days > 0 ? `${days} days and ` : ''

    return (
      <>
        <Divider />
        <Text fontSize="14px" pr="3px">
          {farm.lpSymbol} pool starts: {paddedDays}{paddedHours}:{paddedMinutes}:{paddedSeconds}
        </Text>
      </>
    )
  }
  let { startTime } = farm
  if (!startTime) startTime = 0
  // setStartTime(500000000);
  const poolActive = startTime * 1000 - Date.now() <= 0 // startTime * 1000 - Date.now() <= 0

  const renderButton = !poolActive ? (
    <Countdown date={new Date(startTime * 1000)} renderer={renderer} />
  ) : (
    renderApprovalOrStakeButton()
  )

  return (
    <Action>
      <Flex>
        <Text bold textTransform="uppercase" color="secondary" fontSize="12px" pr="3px">
          {earnToken}
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          Earned
        </Text>
      </Flex>
      <HarvestAction earnings={earnings} farm={farm} />
      <Flex>
        <Text bold textTransform="uppercase" color="secondary" fontSize="12px" pr="3px">
          {lpName}
        </Text>
        <Text bold textTransform="uppercase" color="textSubtle" fontSize="12px">
          Staked
        </Text>
      </Flex>
      {!account ? <UnlockButton mt="8px" fullWidth /> : renderButton}
    </Action>
  )
}

export default CardActions
