import React, { useEffect, useCallback } from 'react'
import { Route, useRouteMatch } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import { Heading } from 'uikit'
import { BLOCKS_PER_YEAR } from 'config'
import FlexLayout from 'components/layout/Flex'
import { Farm, FarmWithStakedValue } from 'state/types'
import Page from 'components/layout/Page'
import { calculateTotal, useFarms, usePrices } from 'state/hooks'
import useRefresh from 'hooks/useRefresh'
import { FarmType, QuoteToken } from 'config/constants/types'
import FarmCard from './components/FarmCard/FarmCard'
import FarmTabButtons from './components/FarmTabButtons'
import { fetchFarmUserDataAsync } from '../../state/farms'

interface FarmProps {
  isSSO: boolean
}

const Farms: React.FC<FarmProps> = ({ isSSO }) => {
  const { path } = useRouteMatch()
  const farmsLP = useFarms()
  const prices = usePrices()
  const { account, ethereum }: { account: string; ethereum: provider } = useWallet()
  const dispatch = useDispatch()
  const { fastRefresh } = useRefresh()

  useEffect(() => {
    if (account) {
      dispatch(fetchFarmUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])
  const activeFarms = isSSO
    ? farmsLP.filter((farm) => farm.type === FarmType.SHARE)
    : farmsLP.filter((farm) => farm.type === FarmType.PARTNERS)
  const inactiveFarms = farmsLP.filter((farm) => farm.type === FarmType.DOLLAR)

  // /!\ This function will be removed soon
  // This function compute the APY for each farm and will be replaced when we have a reliable API
  // to retrieve assets prices against USD
  const farmsList = useCallback(
    (farmsToDisplay) => {
      const farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map((farm: Farm) => {
        const cakeRewardPerBlock = new BigNumber(farm.tokenPerBLock || 1)
          .times(new BigNumber(farm.poolWeight))
          .div(new BigNumber(10).pow(18))
        const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR)
        // eslint-disable-next-line no-nested-ternary
        const baseToken = isSSO ? QuoteToken.SSO : farm.type === FarmType.PARTNERS ? QuoteToken.BNB : QuoteToken.SDO

        let apy = prices[baseToken].times(cakeRewardPerYear)
        const totalValue = calculateTotal(farm, prices)

        if (totalValue.comparedTo(0) > 0) {
          apy = apy.div(totalValue)
        }

        return { ...farm, totalValue, apy }
      })

      return farmsToDisplayWithAPY.map((farm) => (
        <FarmCard key={farm.pid} farm={farm} ethereum={ethereum} account={account} isSSO={isSSO} />
      ))
    },
    [prices, account, ethereum, isSSO],
  )

  return (
    <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
          {isSSO ? ' Pick a Share Bank' : ' Pick a Bank'}
        </Heading>
        {!isSSO && <FarmTabButtons />}
      </FlexLayout>
      <FlexLayout>
        <Route exact path={`${path}`}>
          {farmsList(activeFarms)}
        </Route>
        {!isSSO && (
          <Route exact path={`${path}/token`}>
            {farmsList(inactiveFarms)}
          </Route>
        )}
      </FlexLayout>
    </Page>
  )
}

export default Farms
