import React from 'react'
import styled from 'styled-components'
import { Text, Flex, Link, LinkExternal } from 'uikit'
import { QuoteToken, Swap } from 'config/constants/types'
import contracts from 'config/constants/contracts'
import { FarmWithStakedValue } from 'state/types'

export interface ExpandableSectionProps {
  farm: FarmWithStakedValue
}

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

const Wrapper = styled.div`
  margin-top: 24px;
`

const StyledLinkExternal = styled(LinkExternal)`
  text-decoration: none;
  font-weight: normal;
  color: ${({ theme }) => theme.colors.text};
  display: flex;
  align-items: center;

  svg {
    padding-left: 4px;
    height: 18px;
    width: auto;
    fill: ${({ theme }) => theme.colors.primary};
  }
`

const getSwapUrl = (swap: Swap) => {
  if (swap === Swap.HYPR) return 'https://swap.hyperjump.fi/#/'
  if (swap === Swap.SLIME) return 'https://dex.slime.finance/#/'

  return 'https://exchange.pancakeswap.finance/#/'
}

const getUrls = ({ tokenSymbol, quoteTokenSymbol, isTokenOnly, lpAddresses, swap }: FarmWithStakedValue) => {
  const firstPart = quoteTokenSymbol === QuoteToken.BNB ? 'ETH' : contracts[quoteTokenSymbol][CHAIN_ID]
  const secondPart = contracts[tokenSymbol][CHAIN_ID]

  const bscScanAddress = isTokenOnly
    ? `https://bscscan.com/token/${secondPart}`
    : `https://bscscan.com/token/${lpAddresses[CHAIN_ID]}`

  return [`${getSwapUrl(swap)}add/${firstPart}/${secondPart}`, bscScanAddress]
}

const DetailsSection: React.FC<ExpandableSectionProps> = ({ farm }) => {
  const [liquidityUrlPathParts, bscScanAddress] = getUrls(farm)
  const totalValueFormated = farm.totalValue
    ? `$${Number(farm.totalValue).toLocaleString(undefined, { maximumFractionDigits: 0 })}`
    : '-'

  return (
    <Wrapper>
      <Flex justifyContent="space-between">
        <Text>Stake:</Text>
        <StyledLinkExternal href={liquidityUrlPathParts}>{farm.lpSymbol}</StyledLinkExternal>
      </Flex>
      <Flex justifyContent="space-between">
        <Text>Total Liquidity:</Text>
        <Text>{totalValueFormated}</Text>
      </Flex>
      <Flex justifyContent="flex-start">
        <Link external href={bscScanAddress} bold={false}>
          View on BscScan
        </Link>
      </Flex>
    </Wrapper>
  )
}

export default DetailsSection
