import { useCallback } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useDispatch } from 'react-redux'
import { Farm } from 'state/types'
import { unstake } from 'utils/callHelpers'
import { fetchFarmUserDataAsync } from 'state/farms'
import { useMasterchef } from './useContract'

const useUnstake = ({ contractPid, type }: Farm) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleUnstake = useCallback(
    async (amount: string) => {
      const txHash = await unstake(masterChefContract, contractPid, amount, account)
      dispatch(fetchFarmUserDataAsync(account))
      console.info(txHash)
    },
    [account, dispatch, masterChefContract, contractPid],
  )

  return { onUnstake: handleUnstake }
}

export default useUnstake
